@mixin colorizeElement($background-color, $foreground-color) {
    /* Background */
    background-color: $background-color;

    /* Foreground */
    color: $foreground-color;
}

@mixin generateThemeVariables ($themeName, $themeMap) {

    @each $key,
    $value in $themeMap {
        --theme-#{$themeName}-#{$key}: #{$value};

        $item: (#{$themeName}-#{$key}: $value);
        $themes: map-merge($themes, $item) !global;
    }
}